import React from 'react';
// import Header from './Header';
import Input from './Input';

const App = () => {
	const date = new Date();
	const hours = date.getHours();
	let timeOfDay;

	const h1 = {
		color: "yellow",
	}

	if (hours < 12) {
		timeOfDay = 'Morning';
		h1.backgroundColor = 'blue'
	}

	else if (hours >= 12 && hours < 20) {
		timeOfDay = 'Afernoon';
		h1.backgroundColor = 'orange'
	}
	else {
		timeOfDay = 'Night';
		h1.backgroundColor = 'black'
	}

	return (
		<div>
			<h1 style={h1}>Good {timeOfDay} !</h1>
			<Input />
		</div>);
}

export default App;
